import {
	myRequestPost
} from '@/utils/request.js'
export default {
	data() {
		return {
			id: "",
			newsList: {}
		}
	},
	onLoad(options) {
		this.order = options.order
		this.getNewsList()
	},
	methods: {
		async getNewsList() {
			const res = await myRequestPost("/rest/ops/api/source/getCourseDetail", {
				"seriesId": "1540452695",
				"orderNo": this.order,
				"userId": null
			})
			this.newsList = res.payload
		},
		addcount() {
			this.newsList.videoWatchcount = this.newsList.videoWatchcount + 1
		},
		goWode() {
			uni.switchTab({
				url: `../my/my`
			})
		},
		goShouye() {
			uni.switchTab({
				url: `../index/index`
			})
		}
	}
}
